import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
let CandidateList = resolve => require.ensure([] , () => resolve(require('../components/CandidateList')));
let Interview = resolve => require.ensure([] , () => resolve(require('../components/Interview')));
let Login = resolve => require.ensure([] , () => resolve(require('../components/Login.vue')));
let TemporaryRoom=resolve => require.ensure([] , () => resolve(require('../components/TemporaryRoom.vue')));
let Set=resolve => require.ensure([] , () => resolve(require('../components/Set.vue')));
let DeviceTest=resolve => require.ensure([] , () => resolve(require('../components/DeviceTest.vue')));
let VideoCall=resolve => require.ensure([] , () => resolve(require('../components/VideoCall.vue')));

// const CollectionDetail = resolve => require.ensure([], () => {
//   resolve(require('../components/CollectionDetail'))
// }, 'collectiondetail')
export default new Router({
  routes: [
    {
      path: '/candidate-list',
      name: 'CandidateList',
      component: CandidateList
    },
    {
      path: '/interview',
      name: 'Interview',
      component: Interview
    },
    {
      path: '/temporaryRoom',
      name: 'TemporaryRoom',
      component: TemporaryRoom
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path:'/set',
      name:'Set',
      component:Set
    },
    {
      path: '/device-test',
      name: 'DeviceTest',
      component: DeviceTest
    },
    {
      path: '/video-call',
      name: 'VideoCall',
      component: VideoCall
    },
  ]
})



