<script>
	export default{
		name:'Navigation',
		data(){
			return {
				logoPath: require('../../assets/images/logo-default.png'),
				interviewer:'北京奇虎科技有限公司面试官',
				isChange:0
			}
		},
		props:{
			routerList:{
				default:() =>{
					return [
					{text:'首页' , icon:require('../../assets/images/home.png') , routerName:''},
					{text:'面试' , icon:require('../../assets/images/interview.png') , routerName:'TemporaryRoom'},
					{text:'设置' , icon:require('../../assets/images/set.png') , routerName:'Set'},
				]
				}
			},
			isShow:{default:()=>{return true;} , type:Boolean}
		},
		methods:{
			clickItem(index){
				this.isChange = index;
			},
			exit(){
				this.$router.push({name:'Login'});
			}
		}
	}
</script>
<template>
	<div class="navigation" v-if="isShow">
		<img src="../../assets/images/zl-logo.png" class="zl-logo">
		<div class="logo">
			<img :src="logoPath">
			<p>{{interviewer}}</p>
		</div>
		<ul class="navigation-content">
		<router-link v-for="(item , index) in routerList" :to="{name: item.routerName}" :key="index" >
			<li @click="clickItem(index)" :style="{background:isChange == index ? '#3780d2' : ''}">
				<img :src="item.icon">
				<span>{{item.text}}</span>
			</li>
		</router-link>
		</ul>
		<div class="bottom" @click="exit">
			<img src="../../assets/images/return.png">
			<span>退出当前账号</span>
		</div>
	</div>
</template>
<style lang="less">
	.navigation{
		float: left;
		position: relative;
		height: 100%;
		font-size: 16px;
		background:#408ee6;
		color:#ffffff;
		width: 240px;
		text-align: center;
		.zl-logo{
			margin-top: 19px;
		}
		.logo{
			width: 160px;
			text-align: center;
			margin: auto;
			margin-top: 68px;
			img{
				width: 68px;
				height: 68px;
				border-radius: 50%;
				border:7px solid #fff;
			}
			p{
				text-align: center;
				margin-top: 19px;
			}
		}
		.navigation-content{
			margin-top: 30px;
			li{
				list-style-type: none;
				padding: 18px 120px 18px 45px;
				text-align: left;
				color: #fff;
				/*&:hover{
					background:#3780d2;
				};*/
				img{
					vertical-align: middle;
				}
				span{
					vertical-align: middle;
					float: right;
					margin-top: 1px;
				}
			}
			a:nth-child(2){
				li{
					img{
						margin-left: 2px;
					}
				}
			}
		}
		.bottom{
			position: absolute;
			bottom: 60px;
			left: 50%;
			margin-left: -59.8px;
			font-size: 14px;
			cursor: pointer;
			img{
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				margin-left: 10px;
			}
		}
		& + div{
			float: left;
			width: calc(~"100% - 240px");
		}
	}
</style>


