<template>
  <div id="app">
  	<navigation :isShow="isShow"/>
    <router-view/>
  </div>
</template>

<script>
	import Navigation from './components/common/Navigation'
	export default {
	  name: 'App',
	  components:{
	  	'navigation': Navigation,
	  },
	  data(){
	  	return{isShow:window.location.href.indexOf('login') == -1} 
	  }
	}
</script>

<style lang="less">
	@import 'assets/css/base.less';
</style>



